                <input id="signUpPwd" name="signUpPwd" type="password" class="input" data-type="password" placeholder="전화번호 뒷 4자리">
            </div>
            <div class="group">
              <input id="check" type="checkbox" class="check" checked>
              <label for="check" id="check_font"><span class="icon"></span>아이디 기억하기</label>
            </div>
            <div class="group">
              <input type="submit" class="button" value="로그인">
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
  </div>
</template>

<script>
  export default {
    name: "Login",
    methods: {



    }
  }
</script>

<style scoped>
   @import "../assets/css/login.css";
</style>



